<template>
  <el-container>
    <el-main class="background">
      <div class="main">
        <el-card :body-style="{ padding: '0' }">
          <!--  -->
          <div align="center" class="header">
            <el-row>
              <el-col :span="2" align="start">
                <div>
                  <el-button @click="goBack" type="text" class="back-button">
                    <i class="el-icon-back"></i>
                  </el-button>
                </div>
              </el-col>
              <el-col :span="22" align="center">
                <div class="title">{{name}}</div>
              </el-col>
            </el-row>
          </div>
          <!--  -->
          <div class="cardbody">
            <!--  -->
            <el-row style="margin-top: 3px;">
              <el-col :span="4" align="center">
                <div
                  style="font-size: 14px; margin-left: 20px;margin-top: 13px; font-weight: 450; letter-spacing: 1px"
                >{{totalScore[index]}}</div>
              </el-col>
              <el-col :span="13" align="end" style="height: 10px">
                <div v-if="notSelect">
                  <el-button
                    type="text"
                    @click="index--"
                    :class="index === 0? 'buttonHid' : 'switch-button'"
                  >
                    <i class="el-icon-caret-left"></i>
                  </el-button>
                </div>
                <div v-else>&nbsp;</div>
              </el-col>
              <el-col :span="5" align="center">
                <div
                  v-if="notSelect && studentInfo.length > 0"
                  @dblclick="notSelect = false"
                  class="student-info"
                >{{studentInfo[index].id}} {{studentInfo[index].name}}</div>
                <div v-else class="student-option">
                  <el-select v-model="index" placeholder="" filterable size="small">
                    <el-option
                      v-for="item in studentOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </div>
              </el-col>
              <el-col :span="2" align="start">
                <div v-if="notSelect">
                  <el-button
                    type="text"
                    @click="index++"
                    :class="index === studentInfo.length - 1? 'buttonHid' : 'switch-button'"
                  >
                    <i class="el-icon-caret-right"></i>
                  </el-button>
                </div>
                <div v-else>
                  <el-button @click="notSelect = true" class="cancel-button" type="text">
                    <i class="el-icon-circle-close"></i>
                  </el-button>
                </div>
              </el-col>
            </el-row>
            <!--  -->
            <div
              v-if="question.length > 0 && studentInfo.length > 0 && question.length === studentInfo[index].answer.length"
            >
              <div v-for="(question, i) in question" :key="i" align="start" class="question">
                <div
                  style="font-weight: 400; letter-spacing: 0.8px; font-size: 14px"
                >{{question.order}}.{{question.content}}({{question.score}})</div>
                <div class="answer">{{studentInfo[index].answer[i].content}}</div>
                <div style="margin-top: 15px">
                  <span class="notice"></span>
                  <span style="margin-left: 10px">
                    <el-select
                      v-model="studentInfo[index].score[i]"
                      size="small"
                      style="width: 100px"
                    >
                      <el-option
                        v-for="item in scoreOptions"
                        :key="item.value * 10"
                        :label="item.label"
                        :value="item.value * question.score"
                      ></el-option>
                    </el-select>
                  </span>
                </div>
              </div>
            </div>
          </div>
          <div style="margin-top: 20px; padding-bottom: 15px;" align="center">
            <el-button type="primary" class="submit-button" size="small" @click="submit"></el-button>
          </div>
        </el-card>
      </div>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: "exerciseMark",
  data() {
    return {
      // id
      chapterID: 0,
      classID: 0,
      name: "",
      // 
      notSelect: true,
      // getExercises
      question: [],
      index: 0,
      // getStudentInfo
      studentInfo: [],
      studentOptions: [],
      // loading
      submitLoading: false,
      questionLoading: false,
      studentLoading: false,
      // 
      scoreOptions: [
        {
          value: "1",
          label: ""
        },
        {
          value: "0.9",
          label: ""
        },
        {
          value: "0.8",
          label: ""
        },
        {
          value: "0.6",
          label: ""
        },
        {
          value: "0",
          label: ""
        }
      ]
    };
  },
  methods: {
    // 
    goBack() {
      if (window.history.length <= 1) {
        this.$router.push({ path: "/" });
        return false;
      } else {
        this.$router.go(-1);
      }
    },
    // 
    getExercises() {
      this.question = [];
      this.$http
        .get(
          // chapterid
          "http://10.60.38.173:8765/question/view?chapterId=" + this.chapterID + "&type=review",
          {
            headers: {
              Authorization: "Bearer " + localStorage.getItem("token")
            }
          }
        )
        .then(
          response => {
            if (response.status === 200) {
              let exerciseList = JSON.parse(response.bodyText);
              if (exerciseList.state === 1) {
                let i = 0;
                while (i < exerciseList.data.length) {
                  if (exerciseList.data[i].exercise.exerciseType === 6) {
                    this.question.push({
                      id: exerciseList.data[i].exercise.exerciseId,
                      content: exerciseList.data[i].exercise.exerciseContent,
                      score: exerciseList.data[i].exercise.exercisePoint,
                      order: exerciseList.data[i].exercise.exerciseNumber
                    });
                  }
                  i++;
                }
                this.getStudentInfo();
                this.question.sort(this.compare("order"));
              }
            } else {
              this.$message({ type: "error", message: "!" });
            }
          },
          response => {
            this.$message({ type: "error", message: "!" });
          }
        );
    },
    getStudentInfo() {
      this.studentInfo = [];
      this.studentOptions = [];
      this.$http
        .get(
          // 
          "http://10.60.38.173:8765/getStudentsByClassID?courseClassID=" + this.classID,
          {
            headers: {
              Authorization: "Bearer " + localStorage.getItem("token")
            }
          }
        )
        .then(
          response => {
            if (response.status === 200) {
              let studentList = JSON.parse(response.bodyText);
              if (studentList.state === 1) {
                let i = 0;
                while (i < studentList.data.length) {
                  this.studentInfo.push({
                    answer: new Array(),
                    score: new Array(),
                    id: studentList.data[i].workID,
                    name: studentList.data[i].name,
                    studentId: studentList.data[i].userID
                  });
                  this.studentOptions.push({
                    value: i,
                    label:
                      studentList.data[i].workID +
                      " " +
                      studentList.data[i].name
                  });
                  this.getAnswers(i);
                  i++;
                }
              }
              console.log(this.studentInfo.length);
              console.log(this.studentOptions.length);
            } else {
              this.$message({ type: "error", message: "!" });
            }
          },
          response => {
            this.$message({ type: "error", message: "!" });
          }
        );
    },
    getAnswers(index) {
      this.$http
        .get(
          "http://10.60.38.173:8765/question/viewSomeAnswer?chapterId=" +
            this.chapterID +
            "&studentId=1" +
            //this.studentInfo[index].studentId +
            "&type=review",
          {
            headers: {
              Authorization: "Bearer " + localStorage.getItem("token")
            }
          }
        )
        .then(
          response => {
            if (response.status === 200) {
              let answer = JSON.parse(response.bodyText);
              if (answer.state === 1) {
                let i = 0;
                while (i < answer.data.length) {
                  if (answer.data[i].exercise.exerciseType === 6) {
                    this.studentInfo[index].answer.push({
                      id: answer.data[i].exercise.exerciseId,
                      content: answer.data[i].answer,
                      order: answer.data[i].exercise.exerciseNumber
                    });
                    this.studentInfo[index].score.push(0);
                  }
                  i++;
                }
                this.studentInfo[index].answer.sort(this.compare("order"));
              } else {
                for (let i = 0; i < this.question.length; i++) {
                  this.studentInfo[index].answer.push({
                    id: this.question[i].id,
                    content: "[[[!]]]",
                    order: this.question[i].order
                  });
                  this.studentInfo[index].score.push(0);
                }
              }
            } else {
              this.$message({ type: "error", message: "!" });
            }
          },
          response => {
            this.$message({ type: "error", message: "!" });
          }
        );
    },
    submit() {
      if (this.studentInfo[this.index].score.length === 0) {
        this.$message({ type: "warning", message: "!" });
        return;
      }
      let scores =
        this.studentInfo[this.index].score.length === 1
          ? this.studentInfo[this.index].score[0]
          : this.studentInfo[this.index].score;
      this.$http
        .post(
          "http://10.60.38.173:8765/question/correctAll",
          {
            scores: scores,
            studentId: 1, //this.studentInfo[this.index].studentId,
            chapterId: this.chapterID,
            type: "review"
          },
          {
            headers: {
              Authorization: "Bearer " + localStorage.getItem("token")
            }
          }
        )
        .then(
          response => {
            if (response.status === 200) {
              this.$message({ type: "success", message: "!" });
            } else {
              this.$message({ type: "error", message: "!" });
            }
          },
          response => {
            this.$message({ type: "error", message: "!" });
          }
        );
    },
    // 
    compare(propertyName) {
      return function(object1, object2) {
        var value1 = object1[propertyName];
        var value2 = object2[propertyName];
        if (value2 < value1) {
          return 1;
        } else if (value2 > value1) {
          return -1;
        } else {
          return 0;
        }
      };
    }
  },
  created() {
    this.chapterID = this.$route.query.chapterID;
    this.classID = this.$route.query.classID;
    this.name = this.$route.query.name;
    this.getExercises();
  },
  computed: {
    totalScore() {
      let result = [];
      for (let i = 0; i < this.studentInfo.length; i++) {
        let sum = 0;
        for (let j = 0; j < this.question.length; j++) {
          sum += this.studentInfo[i].score[j];
        }
        result.push(sum);
      }
      return result;
    }
  }
};
</script>

<style scoped>
.buttonHid {
  visibility: hidden;
}

.main {
  width: 800px;
  margin: 0 auto;
}

.title {
  margin-top: 13px;
  font-size: 14px;
  color: #292929;
  margin-left: -40px;
  font-weight: 450;
  letter-spacing: 1px;
}

.question {
  padding: 15px 40px 15px 40px;
}

.question .notice {
  font-weight: 400;
  letter-spacing: 0.8px;
  font-size: 14px;
}

.question .answer {
  background-color: #fcfcfc;
  min-height: 80px;
  padding: 10px 10px 10px 10px;
  margin-top: 15px;
  font-size: 14px;
  letter-spacing: 0.8px;
}

.background {
  background-image: url("../../assets/background.jpg"); /**/
  background-repeat: no-repeat; /**/
  background-size: cover; /**/
  width: 100%; /* 100%*/
}

.header {
  height: 42px;
  border-bottom: 1px solid #eaeef3;
  word-spacing: 4px;
  padding: 3px 10px 3px 15px;
}

.back-button {
  margin-top: 3px;
  color: #292929;
}

.switch-button {
  margin-top: 13px;
  color: #292929;
  font-size: 16px;
  padding: 0 0 0 0;
}

.student-info {
  color: #41abf1;
  font-size: 14px;
  font-weight: 500;
  margin-top: 13px;
  letter-spacing: 1px;
}

.student-option {
  margin-top: 8px;
}

.cancel-button {
  color: #ccd3dd;
  font-size: 15px;
  margin-left: 5px;
  margin-top: 4px;
}

.cardbody {
  height: 680px;
}

.submit-button {
  background-color: #7cc8fb;
  border-color: #7cc8fb;
}
</style>



