<template>
  <el-row>
    <el-row class="head">
      <el-col :span="2" style="padding-top:20px">
        <i class="el-icon-back" @click="feedbackBack"></i><span style="cursor: pointer" @click="feedbackBack"></span>
      </el-col>
    </el-row>
    <el-row style="padding-top:50px">
      <el-col :span="14" :offset="5">
        <el-row>
          <p>~</p>
          <el-row style="padding-bottom:20px">
              <el-col :span="2" :offset="9">
                  <span style="font-size:13px"></span>
                  </el-col>
              <el-col :span="4">
            <el-rate v-model="rate" :colors="['#99A9BF', '#F7BA2A', '#FF9900']" :allow-half="half"></el-rate></el-col>
          </el-row>
          <el-input type="textarea" :rows="6" placeholder=""
          :autosize="{ minRows: 6, maxRows: 10}" v-model="textarea"></el-input>
        </el-row>
        <el-row style="padding-top:20px">
          <el-col :span="2" :offset="22">
            <el-button size="mini" @click="submit"></el-button>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </el-row>
</template>
<script>
export default {
  name:'feedback',
  data() {
    return {
      courseClassID:0,
      rate: null,
      textarea: "",
      half:true,
    };
  },
  methods: {
    feedbackBack() {
      this.$router.back({
        path: "/student/courseDetail"
      });
    },
    submit(){
       const routerParams = this.$route.query.courseClassID
       this.courseClassID=routerParams
       var params = new URLSearchParams()
        params.append("courseClassID",this.courseClassID)
        params.append("studentId",1)
        params.append("comment",this.textarea)
        params.append('rate',this.rate)
      this.$axios
      .post('http://10.60.38.173:8765/addClassComment',params, {
              headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                Authorization:
                  "Bearer "+localStorage.getItem("token")
              }
            }).then(resp=>{
console.log(resp.data,"feedback");
            if(resp.data.state==1){
              this.$message("")
            }
            }).catch(err=>{
              console.log(err)
            })

    }
  }
};
</script>
<style>
html,
body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}
</style>

<style scoped>
.head {
  height: 60px;
  background-color:#292929;
  color:#fff;
  font-size: 17px;
  font-weight: 700px;
  font-family: 'Courier New', Courier, monospace;
}
</style>




