<template>
  <div>
    <el-row style="padding-top:20px">
      <el-col :span="20" :offset="3">
        <el-row>
		<!---->
		<el-table :data="teachers" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;text-align:center">
			<el-table-column type="selection" width="50">
			</el-table-column>
			<el-table-column  prop="courseID" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="courseName" label="" width="180" sortable>
			</el-table-column>
			<el-table-column prop="teacherName" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="teacherID" label="" width="120"  sortable>
			</el-table-column>
      <el-table-column prop="courseYear" label="" width="140" sortable>
			</el-table-column>
      <el-table-column prop="courseSemester" label="" width="140" sortable>
			</el-table-column>
			<el-table-column prop="startTime" label="" width="150" sortable>
			</el-table-column>
			<el-table-column prop="endTime" label="" width="150" sortable>
			</el-table-column>
		<!-- 	<el-table-column label="" width="70">
				<template slot-scope="scope">
					<el-button type="primary" size="mini" @click="handleView(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column> -->
		</el-table>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  name:'teacherList',
  data() {
    return {
      rate: null,
      textarea: "",
      teachers:[],
      sels:[], 
      listLoading:false,
    };
  },
  created(){
     this.$axios
     .get('http://10.60.38.173:8765/getAllCourses',{
headers: {
            'Authorization':
              "Bearer "+localStorage.getItem("token")
          }
    }).then(resp=>{
      if(resp.data.state==1){
        this.teachers=resp.data.data
      }
    }).catch(err=>{
      console.log(err)
    })
  },
  methods: {
    edit() {
      this.$router.push({
        path: "/adminManage/edit"
      });
    },
    selsChange: function (sels) {
        this.sels = sels;
      },
      handleView(){
        this.$router.push({
        path: "/adminManage/chart"
      })
      }
  }
};
</script>
<style>
html,
body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}
</style>

<style scoped>
.header {
  height: 60px;
  background-color: cadetblue;
  color: #fff;
  font-size: 17px;
  font-weight: 700px;
  font-family: "Courier New", Courier, monospace;
}
.logo {
  /* width:230px; */
  height: 60px;
  font-size: 20px;
  padding-left: 20px;
  padding-right: 20px;
}
.logo-width {
  width: 200px;
}
.subhead {
  font: 12px ;
  font-weight: bold;
  position: absolute;
  letter-spacing: 3px;
}
.icon-right {
  text-align: right;
  float: right;
  line-height: 60px;
  padding-right: 40px;
  font-size: 25px;
}
</style>




