<template>
<div>
  <div style="margin:0 auto;width:700px" v-if="haveRev">
    <h3 align="start">
      
      <span>{{totalPoint}}</span>
    </h3>
    <h4 align="start" style="color:rgb(150,150,150)" v-if="time!=null">{{time}}</h4>
    <el-form>
      <div
        style="margin-top:15px;font-size:14px"
        align="start"
        v-for="(item,index) in exercises"
        :key="index"
      >
        <p style="margin-left:5px">
          <pre>{{index+1}}. {{item.exercise.exerciseContent}}{{item.exercise.exercisePoint}}</pre>
          <span
            v-if="item.exercise.exerciseType===2"
          ></span>
        </p>
        <!--  -->
        <el-form-item
          style="margin-top:10px;margin-left:10px;padding-bottom:10px"
          v-if="item.exercise.exerciseType===1"
        >
          <el-radio-group >
            <el-radio
              v-for="i in item.exerciseChoiceList.length"
              :key="i"
              :value="i - 1"
              :label="i - 1"
            >{{String.fromCharCode(i+64)}}. {{item.exerciseChoiceList[i-1].choice}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <!--  -->
        <el-form-item style="margin-left: 10px" v-else-if="item.exercise.exerciseType===2">
          <el-checkbox-group >
            <el-checkbox
              v-for="i in item.exerciseChoiceList.length"
              :key="i"
              :value="i - 1"
              :label="i - 1"
            >{{String.fromCharCode(i+64)}}.{{item.exerciseChoiceList[i-1].choice}}</el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <!--  -->
        <el-form-item style="margin-left: 10px" v-else-if="item.exercise.exerciseType===3">
          <el-input type="textarea" :rows="4" placeholder=""></el-input>
        </el-form-item>
      </div>
      <el-form-item>
        <el-button type="primary" @click="submitForm('answer')" size="mini" disabled> </el-button>
      </el-form-item>
    </el-form>
   
    <div style="background-color:#545c64;height:110px">
      <p style="padding-top:10px;color:#fff"><span style="font-size:12px;"></span></p>
      <el-rate
        v-model="rate"
        :colors="['#99A9BF', '#F7BA2A', '#FF9900']"
        :allow-half="half"
        style="margin-top:-5px;padding-bottom:10px"
      ></el-rate>
      <el-button size="mini" type="primary" @click="submit" round disabled> </el-button>
    </div>
  </div>
  <div v-else>
      <h3></h3>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      time:null,
      haveRev:false,
      tid:0,
      half: true,
      type: 0,
      rate: 0,
      totalPoint: 0, //
      totalScore: 0, //
      exercises: []
    };
  },
  create() {},
  mounted() {
    this.getRev();
    this.setTime();
  },
  watch: {
    $route(to, from) {
      this.getPre();
      this.setTime();
    }
  },
  methods: {
    setTotalPoint() {
      for (var i = 0; i < this.exercises.length; i++) {
        this.totalPoint += this.exercises[i].exercise.exercisePoint;
      }
      console.log(this.totalPoint, "totalPoint");
    },
    setTime(){
      const index =this.$route.query.index
      var catalog = store.state.catalog
      if(catalog.length!=0 && catalog[index].exerciseDeadline_2!=null){
      this.time = catalog[index].exerciseDeadline_2
      }
    },
     getRev() {
      const tid = this.$route.query.trevid;
      this.tid = tid;
      this.$axios
        .get("http://10.60.38.173:8765/question/view", {
          headers: {
            Authorization:
              "Bearer "+localStorage.getItem("token")
          },
          params: {
            chapterId: tid,
            type: "preview"
          }
        })
        .then(resp => {
          console.log("rev", resp.data);
          this.exercises = resp.data.data;
          var length = this.exercises.length;
          if (length != 0) {
            this.haveRev = true;
          }

          this.setTotalPoint();
        })
        .catch(err => {
          console.log(err);
        });
    },
  }
};
</script>
<style scoped>
.test {
  color: #747a81;
}
</style>



