<template>
  <el-container>
    <div>
      <el-form
        :model="pointForm"
        status-icon
        :rules="pointRules"
        ref="pointForm"
        label-width="100px"
        label-position="top"
        align="start"
        style="width: 700px; margin: 0 auto"
      >
        <el-form-item label="" prop="title">
          <el-input v-model="pointForm.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <!-----  ----->
          <div id="editor"></div>
        </el-form-item>
        <el-form-item align="center">
          <el-button type="primary" size="small" @click="save" :loading="loading"></el-button>
          <el-button size="small" @click="reset"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </el-container>
</template>

<script>
import bus from "../../bus.js";
import WangEditor from "wangeditor";
export default {
  name: "chapterEdit",
  data() {
    return {
      item: 0,
      pointName: "",
      pointContent: "",
      editor: new WangEditor("#editor"),
      // 
      pointForm: {
        title: "",
        content: ""
      },
      pointRules: {
        title: [{ required: true, message: "", trigger: "blur" }],
        content: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      loading: false
    };
  },
  methods: {
    getParams() {
      this.item = this.$route.query.item;
    },
    getContents() {
      this.pointName = "";
      this.pointContent = "";
      this.pointForm = {
        title: "",
        content: ""
      };
      this.$http
        .get("http://10.60.38.173:8765/getChapterByID?chapterID=" + this.item.id, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("token")
          }
        })
        .then(
          response => {
            if (response.status === 200) {
              let content = JSON.parse(response.bodyText);
              if (content.state === 1) {
                this.pointName = content.data.contentName;
                this.pointContent = content.data.content;
                this.pointForm.title = this.pointName;
                this.pointForm.content = this.pointContent;
                if (content.data.content !== null) {
                  this.editor.txt.html(content.data.content);
                } else {
                  this.editor.txt.html("");
                }
              }
            } else {
              this.$message({ type: "error", message: "!" });
            }
          },
          response => {
            this.$message({ type: "error", message: "!" });
          }
        );
    },
    reset() {
      this.pointForm.title = this.pointName;
      this.pointForm.content = this.pointContent;
    },
    save() {
      this.loading = true;
      this.$http
        .post(
          "http://10.60.38.173:8765/alertChapter",
          {
            id: this.item.id,
            courseID: this.item.courseID,
            contentName: this.pointForm.title,
            parentID: this.item.parentID,
            siblingID: this.item.siblingID,
            content: this.pointForm.content
          },
          {
            headers: {
              Authorization: "Bearer " + localStorage.getItem("token")
            }
          }
        )
        .then(
          response => {
            if (response.status === 200) {
              this.editor.txt.html("");
              this.getContents();
              bus.$emit("reloadCatalog", true);
              this.loading = false;
              this.$message({ type: "success", message: "!" });
            } else {
              this.loading = false;
              this.$message({ type: "error", message: "!" });
            }
          },
          response => {
            this.loading = false;
            this.$message({ type: "error", message: "!" });
          }
        );
    }
  },
  created() {
    this.getParams();
    this.getContents();
  },
  mounted() {
    this.editor.customConfig.menus = [
      "bold", // 
      "fontSize", // 
      "fontName", // 
      "italic", // 
      "underline", // 
      "foreColor", // 
      "link", // 
      "quote", // 
      "list", // 
      "justify", // 
      "quote", // 
      "image", // 
      "code", // 
      "table", // 
      "undo", // 
      "redo" // 
    ];
    this.editor.customConfig.onchange = () => {
      this.pointForm.content = this.editor.txt.html();
    };
    this.editor.customConfig.debug =
      location.href.indexOf("wangeditor_debug_mode=1") > 0; // debug
      this.editor.customConfig.zIndex = 10;
    this.editor.create();
  },
  watch: {
    // ,
    $route(val) {
      this.getParams();
      this.getContents();
    }
  },
  beforeRouteUpdate(to, from, next) {
    if (
      this.item.name !== this.pointForm.title ||
      this.item.content !== this.pointForm.content
    ) {
      const answer = window.confirm("");
      if (answer) {
        next();
        return;
      } else {
        next(false);
        return;
      }
    }
    next();
  },
  beforeRouteLeave(to, from, next) {
    if (
      this.item.name !== this.pointForm.title ||
      this.item.content !== this.pointForm.content
    ) {
      const answer = window.confirm("");
      if (answer) {
        next();
        return;
      } else {
        next(false);
        return;
      }
    }
    next();
  }
};
</script>

<style scoped>
</style>



