<template>
  <div>
    <div class="banner">
      <div>
        <div class="banner-text">
          <div  id="top" class="callbacks_container">
            <div style="display: inline-block">
              <h6></h6>
              <h3></h3>
              <div v-if="!getUser()">
                <el-button @click="loginTableVisible = true">JOIN US!</el-button>
              </div>
              <div v-else>
                <el-button @click="$router.push('/student')"></el-button>
                <el-button @click="$router.push('/teacher')" style="margin-left: 10px"></el-button>
                <el-button @click="$router.push('/adminManage')" style="margin-left: 10px"></el-button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <el-dialog :visible.sync="loginTableVisible" width="500px" :center="true">
      <div style="padding: 10px 70px 10px 70px">
        <h2>LOGIN</h2>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-position="left" label-width="0px" style="margin-top: 25px">
          <el-form-item prop="account">
            <el-input type="text" v-model="ruleForm.account" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="checkPass">
            <el-input type="password" v-model="ruleForm.password" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <div style="width:100%;" align="center">
            <el-button type="primary" class="form-button" @click="handleLogin" :loading="logining"></el-button>
          </div>
          <div style="width:100%;" align="center">
            <el-button type="text" class="form-text-button" @click="showRegister"></el-button>
          </div>
        </el-form>
      </div>
    </el-dialog>
    <el-dialog :visible.sync="registerTableVisible" width="900px" :center="true">
      <div style="padding: 10px 70px 10px 70px">
        <h2>REGISTER</h2>
        <el-form :model="ruleForm1" :rules="rules1" status-icon ref="ruleForm1" label-position="left" label-width="80px" style="margin: 50px auto;">
          <el-row :gutter="30">
            <el-col :span="12">
              <el-form-item prop="email" label="">
                <el-input type="text" v-model="ruleForm1.email">
                  <el-button slot="append"></el-button>
                </el-input>
              </el-form-item>
              <el-form-item prop="validCode" label="">
                <el-input type="text" v-model="ruleForm1.validCode"></el-input>
              </el-form-item>
              <el-form-item prop="name" label="">
                <el-input type="text" v-model="ruleForm1.name"></el-input>
              </el-form-item>
              <el-form-item prop="number" label="">
                <el-input type="text" v-model="ruleForm1.number"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item prop="gender" label="">
                <el-select v-model="ruleForm1.gender" style='width: 100%'>
                  <el-option
                    v-for="item in genderOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item prop="auth" label="">
                <el-select v-model="ruleForm1.auth" style='width: 100%'>
                  <el-option
                    v-for="item in authOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item prop="password" label="">
                <el-input type="password" v-model="ruleForm1.password"></el-input>
              </el-form-item>
              <el-form-item prop="repassword" label="">
                <el-input type="password" v-model="ruleForm1.repassword"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <div align="center">
            <el-button type="primary" class="form-button" style="width: 40%" @click="handleRegister" :loading="registering"></el-button>
          </div>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import bus from '../bus.js'
    export default {
      name: "Home",
      data() {
        // 
        let passwordValidity = (rule, value, callback) =>{
          // Evan: value
          // 6-18
          let myReg=/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z_]{6,16}$/;
          if(!value){
            return callback(new Error(''));
          }else if(!myReg.test(value)){
            return callback(new Error('6'));
          }else{
            return callback();
          }
        };
        // 
        let repasswordValidity = (rule, value, callback) =>{
          let myReg=/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z_]{6,16}$/;
          if(!value){
            return callback(new Error(''));
          }
          if(value !== this.ruleForm1.password){
            return callback(new Error(''));
          }else if(!myReg.test(value)){
            return callback(new Error('6'));
          }else{
            callback();
          }
        };
        // 
        let emailValidity = (rule, value, callback) =>{
          var atPos=value.indexOf('@');
          var dotPos=value.indexOf('.');
          var myReg=/^[a-zA-Z0-9_-]+@([a-zA-Z0-9]+\.)+(com|cn|net|org)$/;
          if(!value){
            return callback(new Error(''));
          }else if(!myReg.test(value)){
            return callback(new Error(''));
          }else{
            return callback();
          }
        };
        return {
          logining: false,
          loginTableVisible: false,
          registerTableVisible: false,
          ruleForm: {
            account: '',
            password: ''
          },
          rules: {
            account: [
              {required: true, message: '', trigger: 'blur'},
            ],
            password: [
              {required: true, message: '', trigger: 'blur'},
            ]
          },
          registering: false,
          ruleForm1: {
            email: '',
            name: '',
            gender: '',
            auth: '',
            number: '',
            password: '',
            repassword: '',
            validCode: '',
            imgUrl: '',
          },
          genderOptions:[
            {value:'', label:''},
            {value:'', label:''}
          ],
          authOptions:[{
            value:'',
            label:''
          },{
            value:'',
            label:''
          }],
          rules1: {
            email:[
              { required: true, validator: emailValidity, trigger: 'blur' }
            ],
            name: [
              { required: true, message: '', trigger: 'blur' }
            ],
            number:[
              { required: true, message: '', trigger:'blur'}
            ],
            gender: [
              { required: true, message: '', trigger:'blur'}
            ],
            auth: [
              { required: true, message: '', trigger:'blur'}
            ],
            password:[
              { required: true, validator: passwordValidity, trigger: 'blur' }
            ],
            repassword:[
              { required: true, validator: repasswordValidity, trigger: 'blur' }
            ],
            validCode: [
              { required: false, message: '', trigger:'blur'}
            ]
          },
          validity:{
            email: false,
            name: false,
            gender: false,
            auth: false,
            number: false,
            password: false,
            repassword: false,
            imgUrl: false,
          }
        };
      },
      methods: {
        getUser() {
          return localStorage.getItem('username');
        },
        handleLogin(ev) {
          this.$refs.ruleForm.validate((valid) => {
            let that = this;
            if (valid) {
              that.logining = true;
              that.$http.post('http://10.60.38.173:8765/auth', {
                username: this.ruleForm.account,
                password: this.ruleForm.password
              }, {emulateJSON:false}).then(response => {
                localStorage.setItem('token', response.data.data.token.token);
                localStorage.setItem('username', response.data.data.mail);
                localStorage.setItem('userID', response.data.data.userID);
                localStorage.setItem('name', response.data.data.name);
                localStorage.setItem('workID', response.data.data.workID)
                that.logining = false;
                that.$notify({
                  title: '',
                  message: '',
                  type: 'success',
                  duration: 2000
                });
                this.loginTableVisible = false;
                setTimeout(function () {
                  location.reload();
                  //TODO: 
                }, 2000)
              }, response => {
                console.log('', response);
                that.logining = false;
                that.$notify.error({
                  title: '',
                  message: '',
                  duration: 2000
                });
              });
            }
          });
        },
        handleRegister(ev) {
          this.$refs.ruleForm1.validate((valid) => {
            let that = this;
            if (valid) {
              that.registering = true;
              that.$http.post('http://10.60.38.173:8765/register', {
                mail: this.ruleForm1.email,
                password: this.ruleForm1.password,
                name: this.ruleForm1.name,
                workID: this.ruleForm1.number,
                role: this.ruleForm1.auth,
                gender: this.ruleForm1.gender
              }).then(response => {
                that.registering = false;
                that.$notify({
                  title: '',
                  message: '',
                  type: 'success',
                  duration: 2000
                });
                this.showLogin();
              }, response => {
                console.log('', response);
                that.registering = false;
                that.$notify.error({
                  title: '',
                  message: '',
                  duration: 2000
                });
              });
            }
          });
        },
        showRegister() {
          this.loginTableVisible = false;
          this.registerTableVisible = true;
        },
        showLogin() {
          this.registerTableVisible = false;
          this.loginTableVisible = true;
        },
        getData(val) {
          this.loginTableVisible = val;
        }
      },
      created () {
        bus.$on('loginVisible', (val) => this.getData(val));
      }
    }
</script>

<style scoped>

  .banner {
    background:url(../assets/banner.jpg)no-repeat center center;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    background-size: cover;
    position: relative;
    width: 100%;
    height: 720px;
  }
  .banner-text {
    text-align:left;
  }
  .banner-text h6 {
    font-size: 2.3em;
    color: #fff;
    margin: 6em 0 0.5em 5em;
    font-weight: 600;
    letter-spacing: 8px;
    display: inline-block;
    border-bottom:1px solid #01edc9;
  }
  .banner-text h3 {
    font-size: 5em;
    color: #fff;
    margin: 10px 0 0.5em 2.2em;
    font-weight: 600;
    letter-spacing: 10px;
    text-transform: uppercase;
  }
  .banner-text h3 span {
    display: block;
    font-size: 2em;
  }

  .banner-text button {
    position: relative;
    left: 180px;
    letter-spacing: 4px;
    width: 200px;
    background-color: #01edc9;
    border-color: #01edc9;
    color: #fff;
    font-weight: bold;
  }

  .callbacks_container {
    /*-- agileits --*/
    width: 100%;
  }

  .form-button {
    width:100%;
    margin-top: 20px;
    letter-spacing: 15px;
    text-indent: 15px;
    background-color: #01edc9;
    border-color: #01edc9;
    color: #fff;
    font-weight: 900;
    font-family: ;
  }

  .form-text-button {
    width:100%;
    margin-top: 10px;
    color: #8A8A8A;
    font-weight: 900;
    font-family: ;
  }
</style>



