<template>
  <el-container :style="screenWidth">
    <el-main>
      <div>
        <el-col :span="12" :offset="2">
          <div
            style="height:700px;margin-top:0px;padding-right:10px;border:1px solid #ddd"
            ref="graph"
          ></div>
        </el-col>
        <el-col :span="8" :offset="1">
          <el-card id="edit" style="width:100%;height:650px;left:10%;" body-style="{padding:'0px'}">
            <el-row style="padding-bottom:10px" v-show="isAddCourse">
              <p style="text-align:center;font-size:13px"></p>
              <p style="text-align:center;font-size:12px"></p>
            </el-row>
            <el-row style="padding-bottom:10px" v-show="isEditCourse">
              <p style="text-align:center;font-size:13px;margin-bottom:-3px"></p>
              <el-button @click="addCourseVisible" size="mini" type="text"></el-button>
            </el-row>
            <el-row style="padding-bottom:10px" v-show="isAddClass">
              <p style="text-align:center;font-size:13px"></p>
              <el-button @click="addCourseVisible" size="mini" type="text"></el-button>
            </el-row>
            <el-row style="padding-bottom:10px" v-show="isEditClass">
              <p style="text-align:center;font-size:13px;margin-bottom:-3px"></p>
              <el-button @click="addCourseVisible" size="mini" type="text"></el-button>
            </el-row>

            <el-row style="padding-bottom:10px" v-show="editClassNum">
              <p style="text-align:center;font-size:13px;margin-bottom:-3px"></p>
              <el-button @click="addCourseVisible" size="mini" type="text"></el-button>
            </el-row>
            <el-form
              v-show="isAddCourse"
              :model="ruleForm1"
              :rules="rules12"
              ref="ruleForm1"
              label-width="100px"
              class="demo-ruleForm"
              size="mini"
            >
              <el-form-item label="" prop="courseName">
                <el-input v-model="ruleForm1.courseName"></el-input>
              </el-form-item>
              <el-form-item prop="successor" label="" style="text-align:left">
                <el-select v-model="ruleForm1.successor" multiple collapse-tags placeholder="">
                  <el-option
                    v-for="(item,index) in allCourse"
                    :key="index"
                    :label="item.courseName.courseName"
                    :value="item.courseName.courseName"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item style="margin-left:-100px;padding-top:30px">
                <el-button type="primary" @click="submitForm1('ruleForm1')"> </el-button>
              </el-form-item>
            </el-form>
            <el-form
              v-show="isEditCourse"
              :model="ruleForm2"
              :rules="rules12"
              ref="ruleForm2"
              label-width="100px"
              class="demo-ruleForm"
              size="mini"
            >
              <el-form-item label="" prop="courseName">
                <el-input v-model="ruleForm2.courseName"></el-input>
              </el-form-item>
              <el-form-item prop="successor" label="" style="text-align:left">
                <el-select v-model="ruleForm2.successor" multiple collapse-tags placeholder="">
                  <el-option
                    v-for="(item,index) in filterAllCourse"
                    :key="index"
                    :label="item.courseName.courseName"
                    :value="item.courseName.courseName"
                  ></el-option>
                </el-select>
              </el-form-item>
              <el-form-item style="margin-left:-100px;padding-top:30px">
                <el-button type="primary" @click="submitForm2('ruleForm2')"></el-button>
                <el-button type="text" @click="addCourseVisible"></el-button>
              </el-form-item>
            </el-form>
            <el-form
              v-show="isAddClass"
              :model="ruleForm3"
              :rules="rules34"
              ref="ruleForm3"
              label-width="100px"
              class="demo-ruleForm"
              size="mini"
            >
              <el-form-item label="" prop="teacherName">
                <el-input v-model="ruleForm3.teacherName"></el-input>
              </el-form-item>
              <el-form-item label="" prop="teacherID">
                <el-input v-model="ruleForm3.teacherID"></el-input>
              </el-form-item>
              <el-form-item label="" required>
                <el-col :span="11">
                  <el-form-item prop="courseYear">
                    <el-date-picker
                      v-model="ruleForm3.courseYear"
                      type="year"
                      placeholder=""
                      style="width: 100%;"
                      :picker-options="pickerOptions2"
                    ></el-date-picker>
                  </el-form-item>
                </el-col>
                <el-col :span="2" class="line">-</el-col>
                <el-col :span="11">
                  <el-form-item prop="courseSemester">
                    <el-select
                      v-model="ruleForm3.courseSemester"
                      placeholder=""
                      style="width: 100%;"
                    >
                      <el-option label="" value=""></el-option>
                      <el-option label="" value=""></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
              </el-form-item>
              <el-form-item label="" required>
                <el-col :span="11">
                  <el-form-item prop="startTime">
                    <el-date-picker
                      type="date"
                      placeholder=""
                      v-model="ruleForm3.startTime"
                      style="width: 100%;"
                      :picker-options="startTime(ruleForm3)"
                    ></el-date-picker>
                  </el-form-item>
                </el-col>
                <el-col class="line" :span="2">-</el-col>
                <el-col :span="11">
                  <el-form-item prop="endTime">
                    <el-date-picker
                      type="date"
                      placeholder=""
                      v-model="ruleForm3.endTime"
                      style="width: 100%;"
                      :picker-options="endTime(ruleForm3)"
                    ></el-date-picker>
                  </el-form-item>
                </el-col>
              </el-form-item>
              <el-form-item label="" prop="classNum" style="width:80%">
                <el-input type="num" v-model.number="ruleForm3.classNum" auto-complete="off"></el-input>
              </el-form-item>
              <el-form-item style="margin-left:-100px;padding-top:10px">
                <el-button type="primary" @click="submitForm3('ruleForm3')"></el-button>
                <el-button @click="resetForm('ruleForm3')"></el-button>
              </el-form-item>
            </el-form>
            <!--  <el-row v-show="isDel">
              <el-table :data="allCourse" highlight-current-row @selection-change="selsChange">
                <el-table-column type="selection" width="50"></el-table-column>
                <el-table-column type="index" label="" width="70"></el-table-column>
                <el-table-column prop="courseName" label="" width="180" sortable></el-table-column>
                <el-table-column prop="teacherName" label="" width="120" sortable></el-table-column>
                <el-table-column prop="teacherID" label="" width="120" sortable></el-table-column>
              </el-table>
              <div style="margin-top: 20px">
                <el-button @click="delCourse()"></el-button>
              </div>
            </el-row>-->
            <el-form
              v-show="isEditClass"
              :model="ruleForm4"
              :rules="rules34"
              ref="ruleForm4"
              label-width="100px"
              class="demo-ruleForm"
              size="mini"
            >
              <el-form-item label="" prop="teacherName">
                <el-input v-model="ruleForm4.teacherName"></el-input>
              </el-form-item>
              <el-form-item label="" prop="teacherID">
                <el-input v-model="ruleForm4.teacherID"></el-input>
              </el-form-item>
              <el-form-item label="" required>
                <el-col :span="11">
                  <el-form-item prop="courseYear">
                    <el-date-picker
                      v-model="ruleForm4.courseYear"
                      type="year"
                      placeholder=""
                      style="width: 100%;"
                      :picker-options="pickerOptions2"
                    ></el-date-picker>
                  </el-form-item>
                </el-col>
                <el-col :span="2" class="line">-</el-col>
                <el-col :span="11">
                  <el-form-item prop="courseSemester">
                    <el-select
                      v-model="ruleForm4.courseSemester"
                      placeholder=""
                      style="width: 100%;"
                    >
                      <el-option label="" value=""></el-option>
                      <el-option label="" value=""></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
              </el-form-item>
              <el-form-item label="" required>
                <el-col :span="11">
                  <el-form-item prop="startTime">
                    <el-date-picker
                      type="date"
                      placeholder=""
                      v-model="ruleForm4.startTime"
                      style="width: 100%;"
                      :picker-options="startTime(ruleForm4)"
                    ></el-date-picker>
                  </el-form-item>
                </el-col>
                <el-col class="line" :span="2">-</el-col>
                <el-col :span="11">
                  <el-form-item prop="endTime">
                    <el-date-picker
                      type="date"
                      placeholder=""
                      v-model="ruleForm4.endTime"
                      style="width: 100%;"
                      :picker-options="endTime(ruleForm4)"
                    ></el-date-picker>
                  </el-form-item>
                </el-col>
              </el-form-item>
              <el-form-item style="margin-left:-100px;padding-top:30px">
                <el-button type="primary" @click="submitForm4('ruleForm4')"></el-button>
                <el-button type="text" @click="addCourseVisible"></el-button>
              </el-form-item>
            </el-form>
            <el-row v-show="editClassNum">
              <el-table :data="rowIndex.courseClasses" border style="width: 100%">
                <el-table-column prop="classNum" label="" width="100"></el-table-column>
                <el-table-column prop="classCode" label="" width="150"></el-table-column>
                <el-table-column fixed="right" label="" width="80">
                  <template slot-scope="scope">
                    <el-button @click="delClassNum(scope)" type="text" size="small"></el-button>
                  </template>
                </el-table-column>
                <!-- <el-table-column prop="classNum" label=""></el-table-column> -->
              </el-table>
              <div style="padding-top:20px">
                <el-button @click="isAddClassNum=true" size="small"></el-button>
              </div>
              <el-dialog :visible.sync="isAddClassNum" title="" center width="30%">
                <p style="text-align:center"></p>
                <span slot="footer" class="dialog-footer">
                  <el-button type="primary" @click="addClassNum" size="small"> </el-button>
                  <el-button @click="isAddClassNum=false" size="small"> </el-button>
                </span>
              </el-dialog>
            </el-row>
          </el-card>
        </el-col>
        <el-dialog title="" :visible.sync="dialog1" width="30%">
          <div style="margin-top:-20px">
            
            <el-button type="text" @click="editCourseVisible">
              <span style="font-size:12px"></span>
            </el-button>
            <!--  <el-button type="text" @click="isDelCourse=true">
              <span style="font-size:12px"></span>
            </el-button>-->
            <el-button type="text" @click="addClassVisible">
              <span style="font-size:12px"></span>
            </el-button>
          </div>
          <el-table :data="dupCourse" border @row-click="rowClick">
            <el-table-column label="">
              <el-table-column prop="courseInfo.teacherName" label="" width="100"></el-table-column>
              <el-table-column prop="courseInfo.startTime" label="" width="180"></el-table-column>
              <el-table-column prop="courseInfo.endTime" label=""></el-table-column>
              <!-- <el-table-column prop="courseClasses" label=""></el-table-column> -->
            </el-table-column>
            <!-- <el-table-column prop="classNum" label=""></el-table-column> -->
          </el-table>
        </el-dialog><!-- 
        <el-dialog title="" :visible.sync="isDelCourse" width="30%" center>
          <p style="text-align:center"></p>
          <span slot="footer" class="dialog-footer">
            <el-button type="danger" @click="delCourse" size="small"> </el-button>
            <el-button @click="isDelCourse=false" size="small"> </el-button>
          </span>
        </el-dialog> -->
        <el-dialog title="" :visible.sync="dialog2" width="30%" center>
          <p style="text-align:center">
            <el-button @click="editClassVisible" size="small" type="primary"></el-button>
            <el-button @click="editClassNumVisible" size="small" type="primary"></el-button>
            <el-button @click="delClass" size="small" type="danger"> </el-button>
          </p>
        </el-dialog>
      </div>
    </el-main>
  </el-container>
</template>
<script>
import axios from "axios";
export default {
  name: "courseGraph",
  data() {
    return {
      screenWidth: {
        width: window.screen.width
      },
      isAddCourse: true,
      isAddClass: false,
      courseName: 0, //courseNameID
      isEditCourse: false,
      isEditClass: false,
      isDelCourse: false,
      isAddClassNum: false,
      dialog1: false,
      dialog2: false,
      editClassNum: false,
      dataIndex: 0,
      rowIndex: 0,
      data: [],
      links: [],
      allCourse: [], //
      dupCourse: [],

      loading: true,
      pickerOptions0: this.startTime(),
      pickerOptions1: this.endTime(),
      pickerOptions2: this.courseYear(),
      courses: [],
      //
      relayForm: {},
      //
      classNum: null,
      ruleForm1: {
        courseName: "",
        successor:{}
      },
      ruleForm2: {
        courseName: "",
        successor:{}
      },
      ruleForm3: {
        teacherName: "",
        teacherID: null,
        courseYear: "",
        courseSemester: "",
        startTime: null,
        endTime: null,
        classNum: null
      },
      ruleForm4: {
        teacherName: "",
        teacherID: null,
        courseYear: "",
        courseSemester: "",
        startTime: null,
        endTime: null
      },
      rules12: {
        courseName: [
          { required: true, message: "", trigger: "blur" },
          { min: 1, max: 15, message: " 1  15 ", trigger: "blur" }
        ],
        successor: [
          {
            required: false,
            message: "",
            trigger: "blur"
          }
        ]
      },
      rules34: {
        teacherName: [
          { required: true, message: "", trigger: "blur" },
          { min: 1, max: 10, message: " 1  10 ", trigger: "blur" }
        ],
        teacherID: [
          { required: true, message: "", trigger: "blur" }
        ],
        courseYear: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        courseSemester: [
          { required: true, message: "/", trigger: "change" }
        ],
        startTime: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        endTime: [
          {
            type: "date",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        classNum: [
          { required: true, message: "" },
          { type: "number", message: "" }
        ]
      },
      multipleSelection: []
    };
  },
  created() {
    axios
      .get("http://10.60.38.173:8765/getAllCoursesRelation", {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token")
        }
      })
      .then(resp => {
        if (resp.data.state == 1) {
          this.allCourse = resp.data.data;
          console.log(this.allCourse,"allcourse")
          var length = this.allCourse.length;
          if (length != 0) {
            this.set(length);
          } else {
            this.draw();
          }
        } else {
          this.$message(resp.data.message);
        }
      })
      .catch(err => {
        console.log(err);
      });
  },
  computed: {
    filterAllCourse: function() {
      var filterForm = Object.assign({}, this.allCourse);
      var name = this.data[this.dataIndex];

      delete filterForm[this.dataIndex - 1];

      var length = this.links.length;
      for (var i = 0; i < length; i++) {
        if (this.links[i].source == name) {
          for (var j = 0; j < filterForm.length; j++) {
            if (filterForm[j].courseName.courseName == this.links[i].target) {
              delete filterForm[j];
              break;
            }
          }
        }
      }
      return filterForm;
    }
  },
  mounted() {
    //console.log(this.data);
    //this.draw();
  },
  methods: {
    set(length) {
      // 
      let nodes = this.allCourse.map(i => [
        i.courseName.courseName,
        { level: 0, subCourses: i.subCoursesName.map(k => k.courseName) }
      ]);
      console.log("nodes", nodes)
      for (let j = 0; j < nodes.length; j++) {
        let curr = nodes[j][1];
        if (curr.subCourses.length) {
          curr.subCourses.forEach(p => {
            let findIndex = nodes.findIndex(node => node[0] === p);
            nodes[findIndex] = [
              nodes[findIndex][0],
              Object.assign({}, nodes[findIndex][1], { level: nodes[findIndex][1].level > curr.level ? nodes[findIndex][1].level : curr.level + 1 })
            ];
            console.log("!!!", nodes[findIndex])
          });
        }
      }
      
      //console.log("nodes", nodes)

      let tempData = new Map();
      nodes.forEach(n => {
        tempData.set(n[1].level, []);
      });
      nodes.forEach(n => {
        tempData.set(n[1].level, tempData.get(n[1].level).concat([n[0]]));
      });

      let data = [];
      var width=2800;
      var init=0;
      for (let item of tempData.entries()) {
        var num=item[1].length
        init=1400/num
        item[1].forEach((value, index) => {
          let addData = {
            name: value,
            x: Math.round(init+(width / num) * index),
            y: (parseInt(item[0]) + 1) * 340
          };
          data.push(addData);
        });
      }

      data.push({
        name: "start",
        x: 1400,
        y: 0
      });
      this.data = data;
      console.log("TCL: set -> data", data);

      // 
      for (var i = 0; i < length; i++) {
        var num = this.allCourse[i].preCoursesName.length;
        var name = this.allCourse[i].courseName.courseName;
        if (num == 0) {
          //start
          var addLink = {
            target: name,
            source: "start",
            label: {
              normal: {
                show: false
              }
            }
            /*  lineStyle: {
                            normal: { curveness: 0.2 }
                        } */
          };
          this.links.push(addLink);
        } else {

          //
          for (var j = 0; j < num; j++) {
            var addLink = {
              target: name,
              source: this.allCourse[i].preCoursesName[j].courseName,
              label: {
                normal: {
                  show: false
                }
              }
              /* lineStyle: {
                                normal: { curveness: 0.2 }
                            } */
            };
            this.links.push(addLink);
          }
        }
      }
			console.log("TCL: set -> links", this.links)
      this.draw();
    },
    setAllCourse() {
      axios
        .get("http://10.60.38.173:8765/getAllCoursesRelation", {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("token")
          }
        })
        .then(resp => {
          if (resp.data.state == 1) {
            this.allCourse=resp.data.data
          }
        })
        .catch(err => {
          console.log(err);
        });
    }, 
    initGraph() {
      var init = this.$refs.graph;
      var graph = this.$echarts.init(init);
      return graph;
    },
    startTime(form) {
      return {
        disabledDate: time => {
          if (form.endTime != null) {
            return (
              time.getTime() >= form.endTime || time.getTime() < Date.now()
            );
          } else {
            return time.getTime() < Date.now();
          }
        }
      };
    },
    endTime(form) {
      return {
        disabledDate: time => {
          if (form.startTime != null) {
            return (
              time.getTime() <= form.startTime || time.getTime() < Date.now()
            );
          } else {
            return time.getTime() < Date.now();
          }
        }
      };
    },
    courseYear() {
      return {
        disabledDate: time => {
          return time.getTime() < Date.now();
        }
      };
    },
    setTime(val) {
      var result =
        val.getFullYear() + "-" + (val.getMonth() + 1) + "-" + val.getDate();
      return result;
    },
    getTime(val) {
      var result = val.getFullYear();
      return result;
    },
    getDupCourse() {
      axios
        .get("http://10.60.38.173:8765/getAllCoursesByNameID", {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("token")
          },
          params: {
            courseNameID: this.allCourse[this.dataIndex - 1].courseName
              .courseNameID
          }
        })
        .then(resp => {
          console.log(
            this.allCourse[this.dataIndex - 1].courseName.courseNameID,
            "nameid"
          );
          console.log(resp.data, "resp.data");
          if (resp.data.state == 1) {
            this.dupCourse = resp.data.data;
          }
          console.log(this.dupCourse, "dup");
        })
        .catch(err => {
          console.log(err);
        });
    },
    submitForm1(formName) {//
      this.$refs[formName].validate(valid => {
        if (valid) {
          var length = this.data.length;
          for (var i = 0; i < length; i++) {
            if (this.ruleForm1.courseName == this.data[i].name) {
              if (this.ruleForm1.courseName == "start") {
                alert("start");
              } else {
                alert("");
              }
              return 1;
            }
          }
          this.addCourse();
        } else {
          console.log("error submit!!");
          alert("");
          return false;
        }
      });
      this.resetForm(formName);
    },
    submitForm2(formName) {//
      var length = this.data.length;
      for (var i = 0; i < length; i++) {
        if (
          this.ruleForm2.courseName == this.data[i].name &&
          this.ruleForm2.courseName != this.data[this.dataIndex].name
        ) {
          if (this.ruleForm2.courseName == "start") {
            alert("start");
          } else {
            alert("");
          }
          return 1;
        }
      }
      this.editCourse();
    },
    submitForm3(formName) {//
      //addClass
      this.$refs[formName].validate(valid => {
        if (valid) {
          //=======================
          var form = Object.assign({}, this.ruleForm3);
          console.log(this.ruleForm3, "ruleForm3");
          form.startTime = this.setTime(form.startTime);
          form.endTime = this.setTime(form.endTime);
          form.courseYear = this.getTime(form.courseYear);

          console.log(form);

          var params = new URLSearchParams();
          params.append("teacherID", form.teacherID),
            params.append("courseName", this.courseName),
            params.append("teacherName", form.teacherName),
            params.append("courseYear", form.courseYear),
            params.append("courseSemester", form.courseSemester),
            params.append("startTime", form.startTime),
            params.append("endTime", form.endTime),
            axios
              .post("http://10.60.38.173:8765/addCourse", params, {
                headers: {
                  "Content-Type": "application/x-www-form-urlencoded",
                  Authorization: "Bearer " + localStorage.getItem("token")
                }
              })
              .then(resp => {
                console.log(resp.data, "need");
                if (resp.data.state == 1) {
                  console.log(resp.data, "addClass");
                  var id = resp.data.data.courseID;
                  for (var i = 0; i < form.classNum; i++) {
                    var params = new URLSearchParams();
                    params.append("courseID", id);
                    params.append("classNum", i + 1);
                    axios
                      .post("http://10.60.38.173:8765/addClass", params, {
                        headers: {
                          Authorization:
                            "Bearer " + localStorage.getItem("token")
                        }
                      })
                      .then(resp => {
                        console.log(resp.data, "addClassNum");
                        if (resp.data.state == 1) {
                          this.$message("");
                        } else {
                          this.$message("");
                          return -1;
                        }
                      })
                      .catch(err => {
                        console.log(err);
                      });
                  }
                }
              })
              .catch(err => {
                console.log(err);
              });
        } else {
          console.log("error submit!!");
          return false;
        }
        this.resetForm(formName);
      });
    },
    submitForm4(formName) {//
      this.$refs[formName].validate(valid => {
        if (valid) {
          var form = Object.assign({}, this.ruleForm4);
          //==========================================
          form.startTime = this.setTime(form.startTime);
          form.endTime = this.setTime(form.endTime);
          form.courseYear = this.getTime(form.courseYear);

          var params = new URLSearchParams();
          params.append("courseID", form.courseID),
            params.append("teacherID", form.teacherID),
            params.append("courseName", this.courseName),
            params.append("teacherName", form.teacherName),
            params.append("courseYear", form.courseYear),
            params.append("courseSemester", form.courseSemester),
            params.append("startTime", form.startTime),
            params.append("endTime", form.endTime),
            axios
              .post("http://10.60.38.173:8765/alterCourseInfo", params, {
                headers: {
                  "Content-Type": "application/x-www-form-urlencoded",
                  Authorization: "Bearer " + localStorage.getItem("token")
                }
              })
              .then(resp => {
                if (resp.data.state == 1) {
                  console.log(resp.data, "editClass");
                  if (resp.data.state == 1) {
                    this.$message("");
                  } else {
                    this.$message("");
                    return -1;
                  }
                }
              })
              .catch(err => {
                console.log(err);
              });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
      this.resetForm(formName);
      this.isEditClass = false;
      this.isAddCourse = true;
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    addCourse() {
      //
      var name=this.ruleForm1.courseName
      var num = this.ruleForm1.successor.length
      // //
      if (num == 0) {
        var dataNum=this.data.length
          this.data.forEach((value,index) =>{
            if(value.y==340){
              this.data[index].x= Math.round(1400/(dataNum+1) +(2800 / (dataNum+1)) * index)
            }
          })
        
        var addData={
          name:name,
          x:Math.round(1400/(dataNum+1) +(2800 / (dataNum+1)) * dataNum),
          y:340
        };
        this.data.push(addData)
        //start
        var addLink = {
          target: name,
          source: "start",
          label: {
            normal: {
              show: false
            }
          },
          lineStyle: {
            normal: { curveness: 0.2 }
          }
        };
        this.links.push(addLink)
      } else {
        var max_y=0;
        for(var i=0;i<num;i++){
          this.data.forEach((value,index) =>{
            if(value.name==this.ruleForm1.successor[i]){
              if(value.y>max_y){
                max_y=value.y
              }
            }
          })
        }
        var dataNum=this.data.length
          this.data.forEach((value,index) =>{
            if(value.y==max_y+340){
              this.data[index].x= Math.round(1400/(dataNum+1) +(2800 / (dataNum+1)) * index)
            }
          })
        var addData={
          name:name,
          x:Math.round(1400/(dataNum+1) +(2800 / (dataNum+1)) * dataNum),
          y:max_y+340
        };
        this.data.push(addData)
        for (var i = 0; i < num; i++) {
          var addLink = {
            target: this.ruleForm1.courseName,
            source: this.ruleForm1.successor[i],
            label: {
              normal: {
                show: false
              }
            },
            lineStyle: {
              normal: { curveness: 0.2 }
            }
          };        
          this.links.push(addLink)
        }
      }

      var addCourse = Object.assign({}, this.ruleForm1);
      var params = new URLSearchParams();
      params.append("courseName", addCourse.courseName);
      axios
        .post("http://10.60.38.173:8765/addCourseName", params, {
          headers: {
            "Content-Type": "application/x-www-form-urlencoded",
            Authorization: "Bearer " + localStorage.getItem("token")
          }
        })
        .then(resp => {
          if (resp.data.state == 1) {
            var id = resp.data.data.courseNameID;

            for (var i = 0; i < addCourse.successor.length; i++) {
              for (var j = 0; j < this.allCourse.length; j++) {
                if (
                  addCourse.successor[i] ==
                  this.allCourse[j].courseName.courseName
                ) {
                  axios
                    .get("http://10.60.38.173:8765/addCourseRelation", {
                      params: {
                        courseNameID: id,
                        preCourseNameID: this.allCourse[j].courseName
                          .courseNameID
                      },
                      headers: {
                        Authorization: "Bearer " + localStorage.getItem("token")
                      }
                    })
                    .then(resp => {
                      console.log("links,success");

                      if (resp.data.state == 0) {
                        alert("");
                      }
                    });
                  //break;
                }
              }
            }
            this.$message("");
          }
        })
        .catch(err => {
          console.log("err", err);
        });
      this.test();
    },
    addCourseVisible() {
      if (this.isAddCourse == false) {
        this.isEditCourse = false;
        this.isEditClass = false;
        this.isAddCourse = true;
        this.isAddClass = false;
        this.editClassNum = false;
      }
    },
    editCourseVisible() {
      this.dialog1 = false;
      this.isAddCourse = false;
      this.isEditCourse = true; //course
      this.isAddClass = false;
      this.isEditClass = false;
      this.editClassNum = false;

      var obj = Object.assign({}, this.allCourse[this.dataIndex - 1]);
      this.ruleForm2.courseName = obj.courseName.courseName;
      var length = obj.preCoursesName.length;
      this.ruleForm2.successor = new Array();
      for (var i = 0; i < length; i++) {
        this.ruleForm2.successor[i] = obj.preCoursesName[i].courseName;
      }
      console.log(this.ruleForm2, "form2");
    },
    editCourse() {
      var id = this.allCourse[this.dataIndex - 1].courseName.courseNameID;
      var oldName = this.data[this.dataIndex].name;
      var newName = this.ruleForm2.courseName;
      var length = this.allCourse[this.dataIndex - 1].preCoursesName.length;

      var oldLinks = new Array();
      var oldLinksID = new Array();
      for (var i = 0; i < length; i++) {//
        oldLinks[i] = this.allCourse[this.dataIndex - 1].preCoursesName[
          i
        ].courseName;
        oldLinksID[i] = this.allCourse[this.dataIndex - 1].preCoursesName[
          i
        ].courseNameID;
      } 
      //
      var newLinks = this.ruleForm2.successor; 

      var editCourse = Object.assign({}, this.ruleForm2);

      //courseListdatalinks
      if (newName != oldName) {
            this.data[this.dataIndex].name = newName
            //linksname
            var length = this.links.length
            for (var i = 0; i < length; i++) {
                if (this.links[i].target == oldName) {
                    this.links[i].target = newName
                    break;
                }
                if (this.links[i].source == oldName) {
                    this.links[i].source = newName
                    break;
                }
            }
        console.log(this.data,"this.data-edit");
        var params = new URLSearchParams();
        params.append("courseNameID", id);
        params.append("courseName", newName);
        axios
          .post("http://10.60.38.173:8765/alertCourseName", params, {
            headers: {
              Authorization: "Bearer " + localStorage.getItem("token")
            }
          })
          .then(resp => {
            console.log(resp.data, "editCourseName");
            if (resp.message.state == 1) {
              this.$message("");
            } else {
              this.$message("");
              return -1;
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
      //links
      var isEqual = oldLinks.sort().toString() === newLinks.sort().toString();
      console.log(isEqual);
      if (isEqual == false) {
        //links
        //store.commit("editLinks", { new: newLinks, index: this.dataIndex });
         var newLength = this.newLinks.length

            var array = this.links.filter(function (item) {
                if (item.target != newName) {
                    return item
                }
            })
            console.log(array,"array")
            if (newLength == 0 ) {
                
                var addLink = {
                    target: newName,
                    source: "start",
                    label: {
                        normal: {
                            show: false
                        }
                    },
                    lineStyle: {
                        normal: { curveness: 0.2 }
                    }
                };
                array.push(addLink)
            } else if(newLength!=0){
                for (var i = 0; i < newLength; i++) {
                    var addLink = {
                        target: newName,
                        source: this.newLinks[i],
                        label: {
                            normal: {
                                show: false
                            }
                        },
                        lineStyle: {
                            normal: { curveness: 0.2 }
                        }
                    };
                    array.push(addLink)
                }
            }
            this.links = array
        console.log(this.links, "links");

        //-link
        var tempArray1 = []; //
        var del = [];
        for (var i = 0; i < newLinks.length; i++) {
          tempArray1[newLinks[i]] = true; //newLinkstempArray1 true
        }
        for (var i = 0; i < oldLinks.length; i++) {
          if (!tempArray1[oldLinks[i]]) {
            del.push(oldLinks[i]); //
          } else {
            oldLinksID.splice(i, 1);
          }
        }

        //link
        var tempArray2 = []; //
        var add = [];
        for (var i = 0; i < oldLinks.length; i++) {
          tempArray2[oldLinks[i]] = true; //oldLinkstempArray1 true
        }
        for (var i = 0; i < newLinks.length; i++) {
          if (!tempArray2[newLinks[i]]) {
            add.push(newLinks[i]); //
          }
        }

        //////////////////////////////links
        var delLength = del.length;
        var addLength = add.length;
        var allLength = this.allCourse.length;
        for (var m = 0; m < addLength; m++) {
          for (var n = 0; n < allLength; n++) {
            if (add[m] == this.allCourse[n].courseName.courseName) {
              add[m] = this.allCourse[n].courseName.courseNameID;
              break;
            }
          }
        }
        for (var p = 0; p < delLength; p++) {
          axios
            .get("http://10.60.38.173:8765/deleteCourseRelation", {
              headers: {
                Authorization: "Bearer " + localStorage.getItem("token")
              },
              params: {
                courseNameID: id,
                preCourseNameID: oldLinksID[p]
              }
            })
            .then(resp => {
              console.log("links,del,success", p);
              if (resp.data.state == 0) {
                alert("");
              }
            });
        }
        for (var q = 0; q < addLength; q++) {
          axios
            .get("http://10.60.38.173:8765/addCourseRelation", {
              headers: {
                Authorization: "Bearer " + localStorage.getItem("token")
              },
              params: {
                courseNameID: id,
                preCourseNameID: add[q]
              }
            })
            .then(resp => {
              //console.log("links,add,success", q);
              if (resp.data.state == 0) {
                alert("");
              }
            });
        }
      }
      //
      if (isEqual == false || newName != oldName) {
        console.log(this.allCourse);
        this.isEditCourse = false;
        this.isAddCourse = true;
        this.test();
        this.setAllCourse();
        this.$message("");
      } else {
        alert("");
      }
    },
    rowClick(row) {
      this.dialog2 = true;
      console.log(row, "row");
      this.rowIndex = row;
      console.log(this.rowIndex, "rowIndex");
      //dubCourse
    },
    addClassVisible() {
      this.isAddCourse = false;
      this.isEditCourse = false;
      this.isAddClass = true;
      this.isEditClass = false;
      this.editClassNum = false;

      this.dialog1 = false;
    },
    editClassVisible() {
      this.dialog1 = false;
      this.dialog2 = false;
      this.isAddCourse = false;
      this.isEditCourse = false;
      this.isAddClass = false;
      this.isEditClass = true;
      this.editClassNum = false;

      var b = Object.assign({}, this.rowIndex);
      //var a = this.rowIndex;
      var a = b.courseInfo;
      console.log(a);
      a["courseYear"] = new Date(a["courseYear"]);
      a["startTime"] = new Date(a["startTime"]);
      a["endTime"] = new Date(a["endTime"]);
      this.ruleForm4 = a;
      /* 
      var num = this.rowIndex.courseClasses.length;
      this.ruleForm4.classNum = num; */
      console.log(this.ruleForm4);
    },
    editClassNumVisible() {
      this.dialog1 = false;
      this.dialog2 = false;
      this.isAddCourse = false;
      this.isEditCourse = false;
      this.isAddClass = false;
      this.isEditClass = false;
      this.editClassNum = true;
      //this.editClassNum = false;
    },
    selsChange(arr) {
      this.multipleSelection = arr;
      console.log(arr);
      console.log(this.allCourse);
    },
    addClassNum() {
      this.isAddClassNum = false;
      var params = new URLSearchParams();
      params.append("courseID", this.rowIndex.courseInfo.courseID);
      params.append("classNum", this.rowIndex.courseClasses.length + 1);
      axios
        .post("http://10.60.38.173:8765/addClass", params, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("token")
          }
        })
        .then(resp => {
          console.log("success");
          console.log(resp, "addClassNumresp");
          this.rowIndex.courseClasses.push(resp.data.data);
        });
    },
    delClassNum(data) {
      console.log(this.rowIndex, "rowindex1");
      this.rowIndex.courseClasses.splice(data.$index, 1);
      console.log(this.rowIndex, "rowindex2");
      console.log(data.$index);
     axios
        .get("http://10.60.38.173:8765/deleteClass", {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("token")
          },
          params: {
            courseClassID: data.row.id
          }
        })
        .then(resp => {
          console.log("success");
          console.log(resp, "deleteClassNumresp");
          if (resp.data.state == 1) {
            this.$message(resp.data.message);
          }
        });
    },
    delClass() {
      //
      this.dialog2 = false;
      this.dupCourse.splice(this.rowIndex, 1);
      axios
        .get("http://10.60.38.173:8765/deleteCourse", {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("token")
          },
          params: {
            courseID: this.rowIndex.courseInfo.courseID
          }
        })
        .then(resp => {
          console.log("success");
          console.log(resp, "deleteClassresp");
          if (resp.data.state == 0) {
            alert("");
          }
        });
    },
    test() {
      var option = {
        series: [
          {
            data: this.data,
            links: this.links
          }
        ]
      };
      console.log("test");
      this.initGraph().setOption(option);
    },
    draw() {
      var myChart = this.initGraph();
      var option = {
        title: {
          text: ""
        },
        tooltip: {},
        animationDurationUpdate: 1500,
        animationEasingUpdate: "quinticInOut",
        toolbox: {
          feature: {
            //
            saveAsImage: { show: true }
          }
        },
        series: [
          {
            type: "graph",
            layout: "none",
            top:30,                   
            right:"auto", 
            symbolSize: 30,
            roam: true,
            label: {
              normal: {
                show: true,
                textStyle:{
                  fontColor:"#000"
                }
              }
            },
            edgeSymbol: ["circle", "arrow"],
            edgeSymbolSize: [4, 14],
            edgeSymbolColor:'#000',
            edgeLabel: {
              normal: {
                textStyle: {
                  fontSize: 10,
                  fontColor: "#000"
                }
              }
            },
            data: this.data,
            links: this.links,
            itemStyle:{
              normal:{
                color: "#ec7814",
              }
            },
            lineStyle: {
              normal: {
                opacity: 0.9,
                width: 2,
                curveness: 0
              }
            }
          }
        ]
      };
      myChart.setOption(option);
      var that = this;
      let focusNum=0;
      myChart.on("click", function(params) {
      
        focusNum++
        if (params.dataType == "edge") {
          //that.handleClick(params);
          that.dataIndex = params.dataIndex;
           var value = that.links[that.dataIndex].label.normal.show
            if (value == false) {
                that.links[that.dataIndex].label.normal.show = true
            } else {
                that.links[that.dataIndex].label.normal.show = false
            }
          myChart.setOption(option);
        } else if (params.dataType == "node") {
          if(focusNum%2==1){//
            that.myChart.dispatchAction({
              type: "focusNodeAdjacency",
              //  dataIndex 
              dataIndex: params.dataIndex
              });
          }else if(focusNum%2==0){//
            that.myChart.dispatchAction({
            type: "unfocusNodeAdjacency",
            //  seriesId  seriesIndex  seriesName  series.
            seriesIndex: params.seriesIndex
            });
          }
        }
      });
      //
      document.oncontextmenu = function() {
        return false;
      };
      //
      myChart.on("contextmenu", function(params) {
        that.dataIndex = params.dataIndex;
          if (that.dataIndex == 0) {
            alert("");
          } else {
            //
            that.getDupCourse();
            that.courseName =
              that.allCourse[that.dataIndex - 1].courseName.courseNameID;
            that.dialog1 = true;
          }
      });
      that.myChart = myChart;
    }
  }
};
</script>
