<template>
  <div :style="screenWidth">
    <el-row class="header">
      <el-col :span="10" class="logo logo-width">
        <div>
          <div style="display: inline-block;padding-top:20px">
            <i class="fa fa-leanpub" aria-hidden="true" style="margin-right: 0px;"></i>
            <span style="letter-spacing:3px; font-weight: bold;"></span>
          </div>
          <div style="color: #fff; display: inline-block;">
            <div class="subhead" style="top: 15px"></div>
            <div class="subhead" style="top: 33px;">TEACH AND LEARN</div>
          </div>
        </div>
      </el-col>
      <!--  -->
      <el-col :span="1" class="icon-right">
        <i class="el-icon-share" @click="chart"></i>
      </el-col>
      <!--  -->
      <el-col :span="1" class="icon-right">
        <i class="el-icon-service" @click="teacherList"></i>
      </el-col>
      <!-- / -->
      <el-col :span="1" class="icon-right">
        <i class="el-icon-menu" @click="courseList"></i>
      </el-col>
    </el-row>
    <el-row>
      <router-view class="router-view"></router-view>
    </el-row>
  </div>
</template>
<script>
export default {
  name:'adminManage',
  data() {
    return {
      screenWidth:{
        width:window.screen.width,
      }
    };
  },
  methods: {
    chart() {
      
      this.$router.push('/adminManage/courseAnalysis')
    },
    courseList() {
      this.$router.push('/adminManage/courseList')
    },
    teacherList(){
      this.$router.push('/adminManage/teacherList')
    },
  }
};
</script>
<style>
html,
body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}
</style>

<style scoped>
.header {
  height: 60px;
  background-color: cadetblue;
  color: #fff;
  font-size: 17px;
  font-weight: 700px;
  font-family: "Courier New", Courier, monospace;
}
.logo {
  /* width:230px; */
  height: 60px;
  font-size: 20px;
  padding-left: 20px;
  padding-right: 20px;
}
.logo-width {
  width: 200px;
}
.subhead {
  font: 12px ;
  font-weight: bold;
  position: absolute;
  letter-spacing: 3px;
}
.icon-right {
  text-align: right;
  float: right;
  line-height: 60px;
  padding-right: 40px;
  font-size: 25px;
  cursor: pointer;
}
</style>




