<template>
  <el-container>
    <el-main class="background">
      <el-row
        :gutter="10"
        style="margin: 0 auto; width: 90%; max-height: 685px"
        type="flex"
        justify="center"
      >
        <el-col :span="2" align="right">
          <div>
            <el-button @click="goBack" circle style="box-shadow: 0 0 8px 1px #dbdbdb">
              <i class="el-icon-back"></i>
            </el-button>
          </div>
        </el-col>
        <el-col :span="6" align="right">
          <el-card class="info-card" :body-style="{ padding: '0' }">
            <div class="cardbody">
              <div class="info" align="start">
                <div>
                  <span>{{studentID}}</span>
                  <span>{{studentName}}</span>
                </div>
                <el-row style="margin-top: 3px">
                  <div
                    style="padding-left: 11px; font-size: 12px; color: #8e8e8e"
                  > [{{currentName}}]</div>
                </el-row>
              </div>

              <div style="padding: 10px 20px 10px 20px" align="start">
                <el-tag type="danger" size="small" style="margin-top: 5px"></el-tag>
                <el-tag type="warning" size="small" style="margin-top: 5px"></el-tag>
                <el-tag type="success" size="small" style="margin-top: 5px"></el-tag>
                <el-tag type="info" size="small" style="margin-top: 5px"></el-tag>
              </div>
            </div>
          </el-card>
          <el-card class="select-card" :body-style="{ padding: '0' }">
            <div class="cardbody" align="center">
              <div style="width: 64%" align="start">
                <el-row class="select-title"></el-row>
                <el-row>
                  <el-select v-model="xy" @change="handleComparison" size="small">
                    <el-option
                      v-for="item in xyOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </el-row>
                <el-row>
                  <el-select v-model="gradeAttribute" size="small" class="select">
                    <el-option
                      v-for="item in gradeOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                      :disabled="item.disabled"
                    ></el-option>
                  </el-select>
                </el-row>
                <el-row class="select-title" v-if="gradeAttribute !== 3"></el-row>
                <el-row v-if="gradeAttribute !== 3">
                  <el-select v-model="comparison" size="small" @change="handleGradeAttribute">
                    <el-option
                      :disabled="item.disabled"
                      v-for="item in cOptions"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value"
                    ></el-option>
                  </el-select>
                </el-row>
                <el-row v-if="xy === 1">
                  <el-row class="select-title"></el-row>
                  <el-row>
                    <el-row>
                      <el-select v-model="chapterSettings" size="small" filterable>
                        <el-option
                          v-for="item in chapterOptions"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value"
                        ></el-option>
                      </el-select>
                    </el-row>
                  </el-row>
                </el-row>
                <el-row class="select">
                  <el-button
                    type="primary"
                    @click="getCharts"
                    class="select-button"
                    size="small"
                    :loading="drawLoading"
                  ></el-button>
                </el-row>
              </div>
            </div>
          </el-card>
        </el-col>
        <el-col :span="16">
          <el-card class="content-card" :body-style="{ padding: '0' }">
            <div class="cardbody">
              <el-scrollbar wrap-style="height: 650px; margin-top: 5px" :native="false">
                <div>
                  <el-collapse v-model="activeNames" class="collapse">
                    <el-collapse-item title="" name="1">
                      <el-row type="flex" justify="start" style="padding: 10px 20px 0 50px">
                        <el-select v-model="courseIndex" size="small" @change="handleCourse">
                          <el-option
                            v-for="item in courseOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                          ></el-option>
                        </el-select>
                      </el-row>
                      <el-row>
                        <div id="myChart" class="my-chart"></div>
                      </el-row>
                    </el-collapse-item>
                    <el-collapse-item title="" name="2">
                      <div
                        align="start"
                        class="response"
                        v-for="item in studentInfo"
                        :key="item.id"
                      >
                        <el-row>
                          <div class="title">{{item.chapter}}</div>
                        </el-row>
                        <el-row :gutter="10">
                          <el-col :span="6">
                            <span class="grade">{{item.scorePre}}</span>
                          </el-col>
                          <el-col :span="6">
                            <span class="grade">{{item.scoreRev}}</span>
                          </el-col>
                          <el-col :span="6">
                            <span class="grade">{{item.scoreAvg}}</span>
                          </el-col>
                        </el-row>
                        <el-row>
                          <div>
                            <el-rate
                              v-model="item.rate"
                              disabled
                              show-score
                              text-color="#ff9900"
                              score-template="{value}"
                              class="rate"
                            ></el-rate>
                          </div>
                        </el-row>
                        <el-row style="margin-top: 13px">
                          <div class="text">{{item.comments}}</div>
                        </el-row>
                      </div>
                    </el-collapse-item>
                  </el-collapse>
                </div>
              </el-scrollbar>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: "studentAnalysis",
  data() {
    return {
      // 
      studentID: localStorage.getItem("workID"),
      studentName: localStorage.getItem("name"),
      userID: 2, // localStorage.getItem('userID')
      courseIndex: 0,
      courseID: 0,
      classID: 0,
      //
      currentName: "",
      activeNames: ["1"],
      xyOptions: [
        {
          value: 0,
          label: "",
          xName: "",
          yName: ""
        },
        {
          value: 1,
          label: "",
          xName: "",
          yName: ""
        }
      ],
      cOptions: [
        {
          value: 0,
          label: "",
          disabled: false,
          limit: 4
        },
        {
          value: 1,
          label: "",
          disabled: false,
          limit: 1
        },
        {
          value: 2,
          label: "",
          disabled: false,
          limit: 4
        }
      ],
      gradeOptions: [
        {
          value: 0,
          label: ""
        },
        {
          value: 1,
          label: ""
        },
        {
          value: 2,
          label: ""
        },
        {
          value: 3,
          label: "-",
          disabled: false
        }
      ],
      gradeAttribute: 0,
      xy: 0,
      comparison: 0,
      xData: [],
      courseOptions: [],
      chapterSettings: 0,
      chapterOptions: [],
      studentInfo: [],
      // 
      gradeClass: [83, 84, 85],
      // 
      gradeEnumList: [60, 70, 80, 90],
      //  
      amountList: [[8, 17, 29, 18], [7, 19, 22, 24], [12, 16, 31, 13]],
      colors: ["#0997F7", "#92DD22", "#FFFF33", "#FFAAEE"],
      seriesData: [],
      colorOptions: [],
      legendData: [],
      drawCount: 0,
      drawLoading: false
    };
  },
  methods: {
    goBack() {
      if (window.history.length <= 1) {
        this.$router.push({ path: "/" });
        return false;
      } else {
        this.$router.go(-1);
      }
    },
    // 
    getProcess() {
      this.$http
        .get(
          "http://10.60.38.173:8765/getCurrentProgress?courseClassID=" +
            this.classID +
            "&studentID=" +
            this.userID,
          {
            headers: {
              Authorization: "Bearer " + localStorage.getItem("token")
            }
          }
        )
        .then(
          response => {
            if (response.status === 200) {
              let res = JSON.parse(response.bodyText);
              if (res.state === 1) {
                this.currentName = res.data.contentName;
              }
            } else {
              this.$message({ type: "error", message: "!" });
            }
          },
          response => {
            this.$message({ type: "error", message: "!" });
          }
        );
    },
    getChapterOptions() {
      this.chapterOptions = [];
      this.$http
        .get("http://10.60.38.173:8765/getCourseCatalog?courseID=" + this.courseID, {
          headers: { Authorization: "Bearer " + localStorage.getItem("token") }
        })
        .then(
          response => {
            if (response.status === 200) {
              let courseList = JSON.parse(response.bodyText);
              let i = 0;
              while (i < courseList.data.length) {
                this.chapterOptions.push({
                  value: i,
                  label: courseList.data[i].contentName,
                  id: courseList.data[i].id
                });
                i++;
              }
              this.getStudentInfo(this.userID);
            } else {
              this.$message({ type: "error", message: "!" });
            }
          },
          response => {
            this.$message({ type: "error", message: "!" });
          }
        );
    },
    getCourses() {
      this.$http
        .get("http://10.60.38.173:8765/getStuCourseList?studentID=" + this.userID, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("token")
          }
        })
        .then(
          response => {
            if (response.status === 200) {
              let courseList = JSON.parse(response.bodyText);
              if (courseList.state === 1) {
                let i = 0;
                this.courseID = courseList.data[i].courseInfo.courseID;
                this.classID = courseList.data[i].courseClass.id;
                while (i < courseList.data.length) {
                  this.courseOptions.push({
                    value: i,
                    label: courseList.data[i].courseInfo.courseName,
                    courseID: courseList.data[i].courseInfo.courseID,
                    classID: courseList.data[i].courseClass.id
                  });
                  i++;
                }
                this.getChapterOptions();
                this.getProcess();
              }
            } else {
              this.$message({ type: "error", message: "!" });
            }
          },
          response => {
            this.$message({ type: "error", message: "!" });
          }
        );
    },
    getStudentInfo(studentID) {
      this.studentInfo = [];
      this.xData = [];
      this.$http
        .get(
          "http://10.60.38.173:8765/getCourseScoreAndComment?courseID=" +
            this.courseID +
            "&studentID=" +
            studentID,
          {
            headers: {
              Authorization: "Bearer " + localStorage.getItem("token")
            }
          }
        )
        .then(
          response => {
            if (response.status === 200) {
              let res = JSON.parse(response.bodyText);
              let i = 0;
              while (i < res.data.length) {
                let avg =
                  (Number(res.data[i].studentChapter.totalScore_1) +
                    Number(res.data[i].studentChapter.totalScore_2)) /
                  2;

                this.studentInfo.push({
                  id: res.data[i].studentChapter.id,
                  scorePre: res.data[i].studentChapter.totalScore_1,
                  scoreRev: res.data[i].studentChapter.totalScore_2,
                  scoreAvg: avg,
                  comments: res.data[i].studentChapter.comment,
                  rate: Number(res.data[i].studentChapter.rate),
                  chapter: res.data[i].chapterName,
                  chapterID: res.data[i].studentChapter.chapterID
                });
                this.xData.push(res.data[i].chapterName);
                i++;
              }
              this.getStudentData(this.userID, 0);
            } else {
              this.$message({ type: "error", message: "!" });
            }
          },
          response => {
            this.$message({ type: "error", message: "!" });
          }
        );
    },
    getStudentData(studentID, index) {
      this.$http
        .get(
          "http://10.60.38.173:8765/getCourseScoreAndComment?courseID=" +
            this.courseID +
            "&studentID=" +
            studentID,
          {
            headers: {
              Authorization: "Bearer " + localStorage.getItem("token")
            }
          }
        )
        .then(
          response => {
            if (response.status === 200) {
              let res = JSON.parse(response.bodyText);
              let i = 0;
              while (i < res.data.length) {
                let avg =
                  (Number(res.data[i].studentChapter.totalScore_1) +
                    Number(res.data[i].studentChapter.totalScore_2)) /
                  2;
                if (this.xy === 0 && this.gradeAttribute !== 3) {
                  switch (this.gradeAttribute) {
                    case 0: {
                      // 
                      this.seriesData[index].data.push(avg);
                      break;
                    }
                    case 1: {
                      // 
                      this.seriesData[index].data.push(
                        res.data[i].studentChapter.totalScore_1
                      );
                      break;
                    }
                    case 2: {
                      // 
                      this.seriesData[index].data.push(
                        res.data[i].studentChapter.totalScore_2
                      );
                      break;
                    }
                    default: {
                      break;
                    }
                  }
                }
                if (this.xy === 0 && this.gradeAttribute === 3) {
                  // 
                  this.seriesData[0].data.push(
                    res.data[i].studentChapter.totalScore_1
                  );
                  this.seriesData[1].data.push(
                    res.data[i].studentChapter.totalScore_2
                  );
                }
                i++;
              }
              this.drawChart();
            } else {
              this.drawLoading = false;
              this.$message({ type: "error", message: "!" });
            }
          },
          response => {
            this.drawLoading = false;
            this.$message({ type: "error", message: "!" });
          }
        );
    },
    // 
    handleComparison() {
      switch (this.xy) {
        // -
        case 0: {
          this.comparison = 0;
          this.xData = new Array();
          this.gradeAttribute = 0;
          this.cOptions[0].disabled = false;
          this.gradeOptions[3].disabled = false;
          this.getCharts();
          break;
        }
        // -
        case 1: {
          this.comparison = 1;
          this.xData = new Array();
          this.gradeAttribute = 0;
          this.gradeOptions[3].disabled = true;
          this.cOptions[0].disabled = true;
          this.chapterSettings = 0;
          this.getCharts();
          break;
        }
        default:
          break;
      }
    },
    getGenderAndClassData(chapterID, index) {
      this.$http
        .get(
          "http://10.60.38.173:8765/getCourseScoreAndCommentByGender?courseID=" +
            this.courseID +
            "&chapterID=" +
            chapterID +
            "&getDetail=0&courseClassID=" +
            this.classID,
          {
            headers: {
              Authorization: "Bearer " + localStorage.getItem("token")
            }
          }
        )
        .then(
          response => {
            if (response.status === 200) {
              let res = JSON.parse(response.bodyText);
              let i = 0;
              if (this.comparison === 1) {
                // 
                switch (this.gradeAttribute) {
                  case 0: {
                    // 
                    let avgBoy =
                      Number(res.data[0].scoreInfo.boyAverage1) +
                      Number(res.data[0].scoreInfo.boyAverage2) / 2;
                    let avgGirl =
                      Number(res.data[0].scoreInfo.girlAverage1) +
                      Number(res.data[0].scoreInfo.girlAverage2) / 2;
                    this.seriesData[0].data[index] = avgBoy.toFixed(2);
                    this.seriesData[1].data[index] = avgGirl.toFixed(2);
                    break;
                  }
                  case 1: {
                    // 
                    this.seriesData[0].data[
                      index
                    ] = res.data[0].scoreInfo.boyAverage1.toFixed(2);
                    this.seriesData[1].data[
                      index
                    ] = res.data[0].scoreInfo.girlAverage1.toFixed(2);
                    break;
                  }
                  case 2: {
                    // 
                    this.seriesData[0].data[
                      index
                    ] = res.data[0].scoreInfo.boyAverage2.toFixed(2);
                    this.seriesData[1].data[
                      index
                    ] = res.data[0].scoreInfo.girlAverage2.toFixed(2);
                    break;
                  }
                  default: {
                    break;
                  }
                }
              }
              if (this.comparison === 2) {
                // 
                switch (this.gradeAttribute) {
                  case 0: {
                    // 
                    let avg =
                      Number(
                        res.data[0].scoreInfo.totalAverage1 +
                          res.data[0].scoreInfo.totalAverage2
                      ) / 2;
                    this.seriesData[1].data[index] = avg.toFixed(2);
                    break;
                  }
                  case 1: {
                    // 
                    let avg = res.data[0].scoreInfo.totalAverage1;
                    this.seriesData[1].data[index] = avg.toFixed(2);
                    break;
                  }
                  case 2: {
                    // 
                    let avg = res.data[0].scoreInfo.totalAverage2;
                    this.seriesData[1].data[index] = avg.toFixed(2);
                    break;
                  }
                  default:
                    break;
                }
              }
              this.drawChart();
            } else {
              this.drawLoading = false;
              this.$message({ type: "error", message: "!" });
            }
          },
          response => {
            this.drawLoading = false;
            this.$message({ type: "error", message: "!" });
          }
        );
    },
    getScoreDistributeData(chapterID) {
      this.$http
        .get(
          "http://10.60.38.173:8765/getCourseScoreAndCommentByGender?courseID=" +
            this.courseID +
            "&chapterID=" +
            chapterID +
            "&getDetail=0&courseClassID=" +
            this.classID,
          {
            headers: {
              Authorization: "Bearer " + localStorage.getItem("token")
            }
          }
        )
        .then(
          response => {
            if (response.status === 200) {
              let res = JSON.parse(response.bodyText);
              let i = 0;
              if (this.comparison === 1) {
                // 
                switch (this.gradeAttribute) {
                  case 0: {
                    // ;
                    this.seriesData[0].data =
                      res.data[0].scoreInfo.boyScoreAvgDistribute;
                    this.seriesData[1].data =
                      res.data[0].scoreInfo.girlScoreAvgDistribute;
                    break;
                  }
                  case 1: {
                    // 
                    this.seriesData[1].data =
                      res.data[0].scoreInfo.girlScoreDistribute1;
                    this.seriesData[0].data =
                      res.data[0].scoreInfo.boyScoreDistribute1;
                    break;
                  }
                  case 2: {
                    this.seriesData[1].data =
                      res.data[0].scoreInfo.girlScoreDistribute2;
                    this.seriesData[0].data =
                      res.data[0].scoreInfo.boyScoreDistribute2;
                    // 
                    break;
                  }
                  default: {
                    break;
                  }
                }
              }
              if (this.comparison === 2) {
                // 
                switch (this.gradeAttribute) {
                  case 0: {
                    // 
                    this.seriesData[0].data =
                      res.data[0].scoreInfo.totalScoreAvgDistribute;
                    break;
                  }
                  case 1: {
                    // 
                    this.seriesData[0].data =
                      res.data[0].scoreInfo.totalScoreDistribute1;
                    break;
                  }
                  case 2: {
                    // 
                    this.seriesData[0].data =
                      res.data[0].scoreInfo.totalScoreDistribute2;
                    break;
                  }
                  case 3: {
                    this.seriesData.push({});
                    this.seriesData[0].name = "";
                    this.seriesData[1] = {
                      name: "",
                      type: "bar",
                      barGap: 0,
                      data: new Array()
                    };
                    this.colorOptions.push(this.colors[1]);
                    this.legendData[0] = "";
                    this.legendData.push("");
                    this.seriesData[0].data =
                      res.data[0].scoreInfo.totalScoreDistribute1;
                    this.seriesData[1].data =
                      res.data[0].scoreInfo.totalScoreDistribute2;
                  }
                  default: {
                    break;
                  }
                }
              }
              this.drawChart();
            } else {
              this.drawLoading = false;
              this.$message({ type: "error", message: "!" });
            }
          },
          response => {
            this.drawLoading = false;
            this.$message({ type: "error", message: "!" });
          }
        );
    },
    handleGradeAttribute() {
      if (this.comparison !== 0 && this.xy !== 1) {
        this.gradeOptions[3].disabled = true;
      } else {
        this.gradeOptions[3].disabled = false;
      }
    },
    handleCourse(val) {
      this.xy = 0;
      this.gradeAttribute = 0;
      this.comparison = 0;
      this.classID = this.courseOptions[val].classID;
      this.courseID = this.courseOptions[val].courseID;
      this.getChapterOptions();
      this.getProcess();
      this.seriesData = [
        {
          name: this.studentName,
          type: "bar",
          barGap: 0,
          data: new Array()
        }
      ];
      this.colorOptions = [this.colors[0]];
      this.legendData = [this.studentName];
    },
    // 
    getCharts() {
      this.xData = [];
      this.seriesData = new Array();
      this.colorOptions = new Array();
      this.legendData = new Array();
      this.drawCount = 0;
      this.drawLoading = true;
      switch (this.xy) {
        // -
        case 0: {
          for (let m = 0; m < this.studentInfo.length; m++) {
            this.xData.push(this.studentInfo[m].chapter);
          }
          switch (this.comparison) {
            // 
            case 0: {
              if (this.gradeAttribute !== 3) {
                // 
                this.seriesData.push({
                  name: this.studentName,
                  type: "bar",
                  barGap: 0,
                  data: new Array()
                });
                this.colorOptions.push(this.colors[0]);
                this.legendData.push(this.studentName.toString());
                this.getStudentData(this.userID, 0);
              } else {
                this.seriesData = new Array(2);
                this.seriesData[0] = {
                  name: "",
                  type: "bar",
                  barGap: 0,
                  data: []
                };
                this.seriesData[1] = {
                  name: "",
                  type: "bar",
                  barGap: 0,
                  data: []
                };
                this.colorOptions.push(this.colors[0]);
                this.colorOptions.push(this.colors[1]);
                this.legendData.push("");
                this.legendData.push("");
                this.getStudentData(this.userID, 0);
              }
              break;
            }
            // 
            case 1: {
              this.seriesData = new Array(2);
              this.seriesData[0] = {
                name: "",
                type: "bar",
                barGap: 0,
                data: new Array()
              };
              this.seriesData[1] = {
                name: "",
                type: "bar",
                barGap: 0,
                data: new Array()
              };
              this.colorOptions.push(this.colors[0]);
              this.colorOptions.push(this.colors[3]);
              this.legendData.push("");
              this.legendData.push("");
              for (let i = 0; i < this.studentInfo.length; i++) {
                this.seriesData[0].data.push(0);
                this.seriesData[1].data.push(0);
                this.getGenderAndClassData(this.studentInfo[i].chapterID, i);
              }
              break;
            }
            // 
            case 2: {
              // 
              this.seriesData = new Array(2);
              this.seriesData[0] = {
                name: this.studentName,
                type: "bar",
                barGap: 0,
                data: new Array()
              };
              this.seriesData[1] = {
                name: "",
                type: "bar",
                barGap: 0,
                data: new Array()
              };
              this.colorOptions.push(this.colors[0]);
              this.legendData.push(this.studentName.toString());
              this.getStudentData(this.userID, 0);
              for (let i = 0; i < this.studentInfo.length; i++) {
                this.seriesData[1].data.push(0);
                this.getGenderAndClassData(this.studentInfo[i].chapterID, i);
              }
              this.colorOptions.push(this.colors[2]);
              this.legendData.push("");
              break;
            }
            default:
              break;
          }
          break;
        }
        // -
        case 1: {
          this.xData = ["60", "60-70", "70-80", "80-90", "90-100"];
          switch (this.comparison) {
            // 
            case 1: {
              this.seriesData = new Array(2);
              this.seriesData[0] = {
                name: "",
                type: "bar",
                barGap: 0,
                data: new Array()
              };
              this.seriesData[1] = {
                name: "",
                type: "bar",
                barGap: 0,
                data: new Array()
              };
              this.colorOptions.push(this.colors[0]);
              this.colorOptions.push(this.colors[3]);
              this.legendData.push("");
              this.legendData.push("");
              this.getScoreDistributeData(
                this.chapterOptions[this.chapterSettings].id
              );
              break;
            }
            // 
            case 2: {
              // 
              this.seriesData = new Array(1);
              this.seriesData[0] = {
                name: "",
                type: "bar",
                barGap: 0,
                data: new Array()
              };
              this.colorOptions.push(this.colors[0]);
              this.legendData.push("");
              this.getScoreDistributeData(
                this.chapterOptions[this.chapterSettings].id
              );
              break;
            }
            default:
              break;
          }
        }
        default:
          break;
      }
    },
    // 
    drawChart() {
      // domecharts
      let myChart = this.$echarts.init(document.getElementById("myChart"));
      myChart.clear();
      this.drawCount++;
      console.log(this.drawCount);
      // 
      myChart.setOption({
        color: this.colorOptions,
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross"
          }
        },
        legend: {
          data: this.legendData
        },
        dataZoom: [
          {
            type: "inside",
            filterMode: "filter",
            xAxisIndex: [0],
            startValue: 0,
            endValue: 7,
            orient: "horizontal",
            minValueSpan: 5
          }
        ],
        toolbox: {
          show: true,
          orient: "vertical",
          right: "20px",
          top: "center",
          itemGap: 20,
          feature: {
            mark: { show: true },
            magicType: { show: true, type: ["line", "bar", "stack", "tiled"] },
            restore: { show: true },
            saveAsImage: { show: true }
          }
        },
        calculable: true,
        xAxis: [
          {
            type: "category",
            name: this.xyOptions[this.xy].xName,
            axisTick: { show: false },
            data: this.xData
          }
        ],
        yAxis: [
          {
            name: this.xyOptions[this.xy].yName,
            type: "value"
          }
        ],
        series: this.seriesData
      });
      this.drawLoading = false;
    },
    // 
    objDeepCopy(source) {
      let sourceCopy = source instanceof Array ? [] : {};
      for (let item in source) {
        if (source.hasOwnProperty(item)) {
          sourceCopy[item] =
            typeof source[item] === "object"
              ? this.objDeepCopy(source[item])
              : source[item];
        }
      }
      return sourceCopy;
    }
  },
  created() {
    this.getCourses();
    this.seriesData.push({
      name: this.studentName,
      type: "bar",
      barGap: 0,
      data: new Array()
    });
    this.colorOptions.push(this.colors[0]);
    this.legendData.push(this.studentName);
  }
};
</script>

<style scoped>
.main {
  padding-top: 20px;
  margin: 0 auto;
  width: 80%;
  height: 650px;
}

.select {
  margin-top: 15px;
}

.select-title {
  font-size: 13px;
  margin: 15px 0 10px 0;
}

.select-button {
  width: 100%;
  background-color: #7cc8fb;
  border-color: #7cc8fb;
}

.background {
  background-image: url("../../assets/background.jpg"); /**/
  background-repeat: no-repeat; /**/
  background-size: cover; /**/
  width: 100%; /* 100%*/
}

.info-card {
  width: 300px;
  height: 200px;
}

.info {
  height: 50px;
  padding: 20px 10px 15px 10px;
  border-bottom: 1px solid #e7edf5;
}

.info > div > span {
  font-size: 24px;
  font-weight: bold;
  color: #41abf1;
  padding-left: 10px;
}

.select-card {
  margin-top: 10px;
  width: 300px;
  height: 480px;
}

.content-card {
  width: 800px;
  height: 690px;
}

.content-card .collapse {
  padding: 0 20px 0 20px;
  margin-top: -5px;
}

.my-chart {
  width: 100%;
  height: 500px;
  margin-top: 10px;
}

.header {
  height: 42px;
  border-bottom: 1px solid #eaeef3;
}

.cardbody {
  height: 680px;
  position: relative;
}

.response {
  border-top: 1px solid #eaeef3;
  padding: 5px 20px 20px 20px;
}

.response .title {
  font-size: 14px;
  font-weight: bold;
  letter-spacing: 1px;
  color: #41abf1;
  padding-top: 15px;
  padding-bottom: 2px;
}

.response .rate {
  zoom: 90%;
  margin-top: 4px;
  margin-bottom: 3px;
}

.response .grade {
  letter-spacing: 0.6px;
  font-size: 12px;
}

.response .text {
  min-height: 100px;
  background-color: #fafafa;
  padding: 5px 10px 5px 10px;
}
</style>



